@import "../common/styles/sass/variables";
@import "../common/styles/sass/mixins";

.containerStyle {
  position: relative;
  display: flex;
  align-content: flex-start;
  align-items: start;
  justify-content: start;

  .tabStyle {
    text-decoration: none;
    height: 100%;
    position: relative;
    padding: 0 15px;
    margin: 0;
    font-family: sans-serif;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    @include text($titleFontFamily, 14pt, normal, .0rem, $textColor);
    @media screen and (max-width: 480px) {
      @include text($titleFontFamily, 12pt, normal, .0rem, $textColor);
      padding: 0 10px;
    }
  }

  .selectionStyle {
    width: 100%;
    height: 100%;
    position: absolute;
    border-radius: 5px;
    top: 0;
    left: 0;
  }
}

